import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';
import { Team } from './team';
import { Game } from './game';
import { Ladder } from './ladder';
import { Tip } from './tip';


import { analyzeAndValidateNgModules } from '@angular/compiler';

@Injectable({
  providedIn: 'root'
})
export class DataServiceService {

  constructor(private http: HttpClient) { }

  getLadders(): Observable<Ladder[]> {

    return this.http.get('https://api.squiggle.com.au/?q=ladder;year=2019;round=11;source=17').pipe(
      map((data: any) => data.ladder.map((item: any) => new Ladder(
        item.team,
        item.mean_rank,
        item.percentage,
        item.wins,
        item.year,
        item.updated,
        item.swarms,
        item.teamid,
        item.round,
        item.rank,
        item.sourceid,
        item.source
      )))
    );
  }

  getTeams(): Observable<Team[]> {

    return this.http.get('https://api.squiggle.com.au/?q=teams').pipe(
      map((data: any) => data.teams.map((item: any) => new Team(
        item.logo,
        item.id,
        item.name,
        item.abbrev,
      )))
    );
  }


  getGames(): Observable<Game[]> {

    return this.http.get('https://api.squiggle.com.au/?q=games;year=2019;round=12').pipe(
      map((data: any) => data.games.map((item: any) => new Game(
        item.complete,
        item.is_grand_final,
        item.tz,
        item.hbehinds,
        item.ateam,
        item.winnerteamid,
        item.hgoals,
        item.updated,
        item.round,
        item.is_final,
        item.hscore,
        item.abehinds,
        item.winner,
        item.ascore,
        item.hteam,
        item.ateamid,
        item.venue,
        item.hteamid,
        item.agoals,
        item.year,
        item.date,
        item.id
      )))
    );
  }
  getGames2() : Observable<Game[]> {
   
    return this.http.get('https://api.squiggle.com.au/?q=games;complete=0').pipe(
      map((data: any) => data.games.map((item: any) => new Game(
        item.complete,
        item.is_grand_final,
        item.tz,
        item.hbehinds,
        item.ateam,
        item.winnerteamid,
        item.hgoals,
        item.updated,
        item.round,
        item.is_final,
        item.hscore,
        item.abehinds,
        item.winner,
        item.ascore,
        item.hteam,
        item.ateamid,
        item.venue,
        item.hteamid,
        item.agoals,
        item.year,
        item.date,
        item.id
      )))
    ); 
}
getTips(): Observable<Tip[]> {

  return this.http.get('https://api.squiggle.com.au/?q=tips;year=2019;source=1;complete=0').pipe(
    map((data: any) => data.tips.map((item: any) => new Tip(
      item.confidence,
      item.bits,
      item.gameid,
      item.ateamid,
      item.venue,
      item.year,
      item.correct,
      item.date,
      item.updated,
      item.hteam,
      item.tipteamid,
      item.margin,
      item.err,
      item.tip,
      item.ateam,
      item.source,
      item.sourceid,
      item.hconfidence,
      item.hteamid,
      item.round
    )))
  );
}

}
